class Card {

    id: number
    html: HTMLElement
    callback: any
    constructor(id: number, callback) {
        this.id = id
        this.html = this.generateHtml()
        this.callback = callback.bind(null, this)
        this.addEvent()
    }
    generateHtml(): HTMLElement {
        const div = document.createElement("div")
        div.classList.add("gameCard")
        div.classList.add(`card-${this.id}`)
        return div
    }
    addEvent() {
        this.html.addEventListener("click", this.callback)
    }
    
    removeEvent(){
        this.html.removeEventListener("click",this.callback)
    }

    flip() {
        if (this.html.classList.contains("selected")) {
            this.html.classList.remove("selected")
            this.addEvent()
        } else {
            this.html.classList.add("selected")
            this.removeEvent()
        }
    }

    
}



export default Card