import Card from "./card"
import swal from 'sweetalert';

class Game {

    container: Element
    cartitas: number
    cardsSelected: Card[] = []
    score: number = 0
    matches: number = 0

    constructor(container: Element, cartitas: number) {
        this.container = container
        this.cartitas = cartitas
        this.cardEventOnClick = this.cardEventOnClick.bind(this)
    }

    start() {
        this.container.innerHTML = ""
        this.score = 0
        this.matches = 0
        const cartas = this.generatorCard()
        cartas.forEach(element => this.container.appendChild(element.html))
    }

    generatorCard(): Array<Card> {
        const cartas: Array<Card> = []

        for (let i = 0; i < this.cartitas; i++) {
            cartas.push(new Card(i, this.cardEventOnClick))
            cartas.push(new Card(i, this.cardEventOnClick))
        }

        for (let i = cartas.length - 1; i > 0; i--) {
            const position2 = Math.floor(Math.random() * (i + 1))
            const value = cartas[i]
            const value2 = cartas[position2]
            cartas[i] = value2
            cartas[position2] = value
        }
        return cartas
    }

    cardEventOnClick(card: Card) {
        if (this.cardsSelected.length == 2) return
        card.flip()
        this.cardsSelected.push(card)

        if (this.cardsSelected.length == 2) this.checkGame()
    }

    checkGame() {

        if (this.cardsSelected[0].id == this.cardsSelected[1].id) {
            this.score += 3
            this.matches++
            this.cardsSelected = []
        } else {

            setTimeout(() => {
                this.cardsSelected.forEach((card) => card.flip())
                this.cardsSelected = []
            }, 1500)
            this.score -= 1
        }

        if (this.matches == this.cartitas) {
            swal("Congratulations! You got " + this.score + " points", {
                buttons: ["Exit", "Play Again"],
            })
            .then((value) => {
                if (value) this.start()
            })
        }
    }
}



export default Game