import "popper.js"
import $ from "jquery";
import "bootstrap"
import Game from "./game"
import { ViewController } from "./viewController"

function startGame() {
    const section = document.querySelector(".card_board")
    const game = new Game(section, 6)
    game.start()
}

const viewController = new ViewController(".nav-link",".pagina")
viewController.listen()

document.getElementById("start").onclick = startGame


$('.navbar-collapse a').click(function(){
    $(".navbar-collapse").collapse('hide');
});