
export class ViewController {

    private buttons: NodeListOf<HTMLElement>;
    pages: HTMLElement[];

    constructor(cssSelector: string, pagesSelector: string) {
        this.buttons = document.querySelectorAll(cssSelector)
        this.pages = Array.from(document.querySelectorAll(pagesSelector))
    }

    listen() {
        this.buttons.forEach(element => {
            element.addEventListener("click", () => {
                this.showPage(element.dataset.pagina)
            })
        })
    }

    showPage(pageid: string) {
        
        const pageselected = this.pages.filter(page => page.id == pageid)[0]
        
        this.pages.forEach(element => {
            element.style.display = "none"
        });
        
        pageselected.style.display = "block"
    }
}